                console.log(fundraisingIntention)


                var lastYearInvestment = this.lastYearInvestment;
                var projectedInvestment = this.currentYear.projectedInvestment;
                var projectedLoans = this.currentYear.projectedLoans;
                var projectedEquipment = this.currentYear.projectedEquipment;
                var newSalesRevenue = this.expectedReturn.newSalesRevenue;
                var newProfits = this.expectedReturn.expectedReturn;
                var newTax = this.expectedReturn.newTax;
                var newForex = this.expectedReturn.newForex;

                var privateEnterprise;
                if (this.privateEnterprise){
                    privateEnterprise = 1;
                }else {
                    privateEnterprise = 0;
                }
                console.log(privateEnterprise)
                var totalEquipmentInvestment = this.aboutForm.totalEquipmentInvestment;
                var equipmentNameNum = this.aboutForm.equipmentNameNum;
                this.$axios.post( baseUrl +'industrialdeclaration/addDeclaration',that.$qs.stringify({
                    totalInvestment:totalInvestment,
                    declarationType:declarationType,
                    isNew:isNew,
                    declarationIndustry:declarationIndustry,
                    industryTypeDetail:industryTypeDetail,
                    companyName:companyName,
                    projectName:projectName,
                    projectContent:projectContent,
                    city:city,
                    district:district,
                    startDate:startDate,
                    endDate:endDate,
                    //基本表
                    'detailEntity.projectType':projectType,
                    'detailEntity.projectTypeDetail':projectTypeDetail,
                    'detailEntity.investmentSelfPrepare':investmentSelfPrepare,
                    'detailEntity.investmentLoans':investmentLoans,
                    'detailEntity.investmentUsingForex':investmentUsingForex,
                    'detailEntity.implementationInvestment':implementationInvestment,
                    'detailEntity.fundsLoans':fundsLoans,
                    'detailEntity.fundraisingIntention':fundraisingIntention,
                    'detailEntity.lastYearInvestment':lastYearInvestment,
                    'detailEntity.projectedInvestment':projectedInvestment,
                    'detailEntity.projectedLoans':projectedLoans,
                    'detailEntity.projectedEquipment':projectedEquipment,
                    'detailEntity.newSalesRevenue':newSalesRevenue,
                    'detailEntity.newProfits':newProfits,
                    'detailEntity.newTax':newTax,
                    'detailEntity.newForex':newForex,
                    'detailEntity.totalEquipmentInvestment':totalEquipmentInvestment,
                    'detailEntity.equipmentNameNum':equipmentNameNum

                }))
                    .then((res)=>{
                        console.log(res)
                        if (res.data.code==0){
                            that.$message.success(res.data.msg);
                        }
                    })
            },
            //所属行业
            handleChange1(){
                console.log(this.value)
                this.baseForm.projectType = this.value[0];
                this.baseForm.projectTypeDetail = this.value[1];
            },
            //所属城市
            handleChange (value) {
                var arr = [];
                value.forEach((item,index)=>{
                    arr.push(CodeToText[item])
                })
                this.form.city = arr;
                if (arr[1] =='市辖区'){
                    this.baseForm.city = arr[0];
                }else {
                    this.baseForm.city = arr[1];
                }
                this.baseForm.district = arr[2];
                console.log(this.form.city)

            },
            //获取二级分类
            getClass2(){
                console.log(this.value);
                console.log(this.value1);
                var value = this.value - 1;
                var value1 = this.value1 -1;
                console.log(this.orginData[value])
            },
            //首页选择值
            geto2Value(){
                console.log(this.o2Value)
            }
        },

    }
</script>

